	module.exports = {"data":{"post":{"html":"<p>A number of folks (well, ok, two) at BlogTalk referred to me as a German blogging pioneer. It made me feel old, but it did earn me a number (two) of beers. My blog cred may not go back as far as that of Rebecca Blood (April 1999), who gave a keynote in Vienna, but I did try out Blogger for a few weeks later that summer. And, surprise, surprise, the <a title=\"Maus Newsletter\" href=\"http://www.papascott.de/blogger99.html\">Maus Newsletter</a> is still in the Blogger database. Just for fun, I decided to publish it (slightly edited for spousal privacy). </p>\n<p>This is not actually the <a title=\"PapaScott: Old Times\" href=\"http://www.papascott.de/2001/09/10/1538.php\">first time</a> I've rediscovered my old Blogger page, though. I guess I have the right to dig it up evey 2 years.</p>","fields":{"title":"Blog Cred since August 1999","date":"27 May 2003","url":"https://papascott.de/2003-05-27-blog-cred-since-august-1999/"}}},"pathContext":{"slug":"/2003-05-27-blog-cred-since-august-1999/","prev":{"fields":{"title":"Wenn die Mutter mit dem Sohn","date":"26 May 2003","slug":"/2003-05-26-wenn-die-mutter-mit-dem-sohn/"}},"next":{"fields":{"title":"City of Munich migrates from NT to Linux","date":"27 May 2003","slug":"/2003-05-27-city-of-munich-migrates-from-nt-to-linux/"}}}}

